import { Image, Tooltip } from "antd";
import React from "react";

const BaseFooter: React.FC = () => (
  <div
    style={{
      display: "flex",
      justifyContent: "center",
      marginBottom: "70px",
    }}
  >
    <div
      style={{
        margin: "0 80px",
      }}
    >
      <Tooltip title="jira 바로가기">
        <Image
          preview={false}
          width={100}
          src="data:image/jpeg;base64,"
        />
      </Tooltip>
    </div>
    <div
      style={{
        margin: "0 80px",
      }}
    >
      <Tooltip title="conflunce 바로가기">
        <Image
          preview={false}
          width={100}
          src="data:image/jpeg;base64,"
        />
      </Tooltip>
    </div>
    <div
      style={{
        margin: "0 80px",
      }}
    >
      <Tooltip title="gitlab 바로가기">
        <Image
          preview={false}
          width={100}
          src="data:image/png;base64,"
        />
      </Tooltip>
    </div>
  </div>
);

export default BaseFooter;
