import { Table, Image, Space } from "antd";
import type { ColumnsType } from "antd/es/table";
import React from "react";

interface DataType {
  key: React.Key;
  name: string;
  pm: string;
}

const columns: ColumnsType<DataType> = [
  {
    title: "프로젝트명",
    dataIndex: "name",
    ellipsis: true,
    width: "50%",
  },
  {
    title: "PM",
    dataIndex: "pm",
    ellipsis: true,
  },
  {
    title: "Links",
    ellipsis: true,
    render: () => (
      <Space>
        <Image
          preview={false}
          width={15}
          src="data:image/jpeg;base64,"
        />
        <Image
          preview={false}
          width={15}
          src="data:image/jpeg;base64,"
        />
        <Image
          preview={false}
          width={15}
          src="data:image/png;base64,"
        />
      </Space>
    ),
  },
];

const data: DataType[] = [
  {
    key: "1",
    name: "On-Site Multi-Access 기술 개발",
    pm: "김동명",
  },
  {
    key: "2",
    name: "TSO팀 Wiki",
    pm: "임재윤",
  },
  {
    key: "3",
    name: "5GX Infra BM Cell",
    pm: "김정규",
  },
  {
    key: "4",
    name: "Autonomous Driving Development",
    pm: "서정석",
  },
];

const MyProj: React.FC = () => (
  <Table columns={columns} dataSource={data} size="small" pagination={false} />
);

export default MyProj;
