import { Typography, Layout, Avatar } from "antd";
import React from "react";
import { BellFilled, UserOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";

const { Title } = Typography;
const { Header } = Layout;

const BaseHeader: React.FC = () => (
  <>
    <Header
      style={{
        position: "fixed",
        zIndex: 1,
        width: "100%",
        minWidth: "448px",
        backgroundColor: "#fff",
        borderBottom: "2px solid #F0F2F5",
        padding: "0 30px 0 25px ",
      }}
    >
      <Title style={{ paddingTop: "7px", color: "#1890FF", float: "left" }}>
        <Link to="/">TDE</Link>
      </Title>
      <div style={{ float: "left", padding: "7px" }}>
        Technology Development Environment
      </div>
      <Avatar
        shape="square"
        icon={<UserOutlined />}
        style={{
          backgroundColor: "#333f50",
          float: "right",
          margin: "15px 10px",
        }}
      />
      <BellFilled
        style={{
          fontSize: "25px",
          color: "#333f50",
          float: "right",
          margin: "18px 8px",
        }}
      />
    </Header>
  </>
);

export default BaseHeader;
