import { Checkbox, Table, Button, Alert } from "antd";
import type { ColumnsType } from "antd/es/table";
import React, { useState } from "react";

interface DataType {
  key: React.Key;
  name: string;
  age: number;
  address: string;
}

const columns: ColumnsType<DataType> = [
  {
    title: "이름",
    dataIndex: "name",
  },
  {
    title: "소속",
    dataIndex: "age",
  },
  {
    title: "구분",
    dataIndex: "address",
  },
  {
    title: "Gitlab",
    children: [
      {
        title: "Admin",
        render: () => <Checkbox />,
      },
      {
        title: "CURD",
        render: () => <Checkbox />,
      },
      {
        title: "Read",
        render: () => <Checkbox />,
      },
    ],
  },
  {
    title: "Atlassian",
    children: [
      {
        title: "Admin",
        render: () => <Checkbox />,
      },
      {
        title: "CURD",
        render: () => <Checkbox />,
      },
      {
        title: "Read",
        render: () => <Checkbox />,
      },
    ],
  },
];

const data: DataType[] = [];
for (let i = 0; i < 20; i++) {
  data.push({
    key: i,
    name: `Edward King ${i}`,
    age: 32,
    address: `London, Park Lane no. ${i}`,
  });
}

const StepOne: React.FC = () => {
  const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
  const [loading, setLoading] = useState(false);

  const start = () => {
    setLoading(true);
    // ajax request after empty completing
    setTimeout(() => {
      setSelectedRowKeys([]);
      setLoading(false);
    }, 1000);
  };

  const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
    console.log("selectedRowKeys changed: ", selectedRowKeys);
    setSelectedRowKeys(newSelectedRowKeys);
  };

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  };

  const hasSelected = selectedRowKeys.length > 0;

  return (
    <div style={{ padding: "20px 0" }}>
      <Button
        type="primary"
        onClick={start}
        disabled={!hasSelected}
        loading={loading}
        style={{ marginBottom: 16 }}
      >
        권한설정
      </Button>
      <Table
        rowSelection={rowSelection}
        columns={columns}
        dataSource={data}
        size="middle"
        bordered
      />
      <Alert
        message="Sikp은 완료 버튼을 누르세요"
        type="info"
        showIcon
        style={{ width: "250px", height: "35px" }}
      />
    </div>
  );
};

export default StepOne;
