import { Layout } from "antd";
import React, { ReactNode, useState } from "react";
import BaseHeader from "./BaseHeader";
import BaseSiderMenu from "./BaseSiderMenu";

interface BaseLayoutProps {
  contents?: ReactNode;
  footer?: ReactNode;
}

const { Footer, Sider, Content } = Layout;

function BaseLayout(props: BaseLayoutProps) {
  const [collapsed, setCollapsed] = useState(true);

  return (
    <>
      <Layout>
        <BaseHeader />
        <Layout>
          <Sider
            trigger={null}
            collapsible
            collapsed={collapsed}
            style={{
              backgroundColor: "#fff",
              borderRight: "2px solid #F0F2F5",
            }}
            onMouseEnter={(e) => {
              setCollapsed(false);
            }}
            onMouseLeave={(e) => {
              setCollapsed(true);
            }}
          >
            <BaseSiderMenu />
          </Sider>
          <Layout>
            <Content
              style={{
                minHeight: "74vh",
                height: "100%",
                padding: "94px 30px 20px",
                backgroundColor: "#fff",
              }}
            >
              {props.contents}
            </Content>

            <Footer
              style={{
                bottom: 0,
                textAlign: "center",
                backgroundColor: "#fff",
              }}
            >
              {props.footer}
              <div>COPYRIGHT © SK TELECOM CO., LTD. ALL RIGHTS RESERVED.</div>
            </Footer>
          </Layout>
        </Layout>
      </Layout>
    </>
  );
}

export default BaseLayout;
