import {
  DesktopOutlined,
  ApartmentOutlined,
  NotificationOutlined,
  HomeOutlined,
} from "@ant-design/icons";
import type { MenuProps } from "antd";
import { Menu } from "antd";
import React from "react";
import { Link } from "react-router-dom";

type MenuItem = Required<MenuProps>["items"][number];

function getItem(
  label: React.ReactNode,
  key: React.Key,
  icon?: React.ReactNode,
  children?: MenuItem[]
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
  } as MenuItem;
}

const items: MenuItem[] = [
  getItem(<Link to="/">홈</Link>, "1", <HomeOutlined />),

  getItem("프로젝트관리", "sub1", <DesktopOutlined />, [
    getItem(<Link to="/projReg">- 프로젝트 등록</Link>, "2"),
    getItem("- 프로젝트 현황", "3"),
  ]),

  getItem("그룹 관리", "4", <ApartmentOutlined />),

  getItem("게시판", "sub2", <NotificationOutlined />, [
    getItem("- 공지사항", "5"),
    getItem("- FAQ", "6"),
    getItem("- Q&A", "7"),
  ]),
];

const BaseSiderMenu: React.FC = () => {
  return (
    <Menu
      mode="inline"
      items={items}
      style={{
        position: "relative",
        top: "75px",
        border: 0,
      }}
    />
  );
};

export default BaseSiderMenu;
