import { Typography, Button, Steps, message } from "antd";
import React, { useState } from "react";
import {
  UsergroupAddOutlined,
  FileTextOutlined,
  ToolOutlined,
} from "@ant-design/icons";
import StepOne from "./StepOne";
import StepTwo from "./StepTwo";
import StepThree from "./StepThree";

const { Step } = Steps;

const { Title } = Typography;

const steps = [
  {
    title: "기본정보",
    content: <StepOne />,
    icon: <FileTextOutlined />,
  },
  {
    title: "멤버등록 ",
    content: <StepTwo />,
    icon: <UsergroupAddOutlined />,
  },
  {
    title: "권한설정",
    content: <StepThree />,
    icon: <ToolOutlined />,
  },
];

const ProjSteps: React.FC = () => {
  const [current, setCurrent] = useState(0);
  const next = () => {
    setCurrent(current + 1);
  };

  const prev = () => {
    setCurrent(current - 1);
  };

  return (
    <div
      style={{
        minHeight: "80vh",
      }}
    >
      <Title
        style={{
          borderBottom: "1px solid #333",
          paddingBottom: "10px",
          color: "#333",
          marginBottom: "20px",
        }}
        level={3}
      >
        프로젝트 등록
      </Title>
      <Steps current={current}>
        {steps.map((item) => (
          <Step key={item.title} title={item.title} icon={item.icon} />
        ))}
      </Steps>
      <div
        style={{
          marginTop: "15px",
          padding: "0 40px",
          border: "1px dashed #F0F2F5",
          borderRadius: "2px",
          position: "relative",
          backgroundColor: "#f4f6fa",
        }}
      >
        {steps[current].content}
      </div>
      <div style={{ marginTop: "24px" }}>
        {current < steps.length - 1 && (
          <Button
            type="primary"
            onClick={() => next()}
            style={{
              margin: "0 0 0 47%",
              position: "relative",
            }}
          >
            다음 &gt;
          </Button>
        )}

        {current === steps.length - 1 && (
          <Button
            type="primary"
            onClick={() => message.success("프로젝트가 등록 되었습니다.")}
            style={{
              margin: "0 0 0 47%",
            }}
          >
            완료
          </Button>
        )}
        {current > 0 && (
          <Button
            style={{
              marginLeft: "5px",
            }}
            onClick={() => prev()}
          >
            이전
          </Button>
        )}
      </div>
    </div>
  );
};

export default ProjSteps;
