import React from "react";
import { Input, Button, DatePicker, Checkbox } from "antd";

const { RangePicker } = DatePicker;

const StepOne: React.FC = () => {
  return (
    <>
      <form action="">
        <table
          style={{
            borderCollapse: "separate",
            borderSpacing: "0 25px",
            width: "100%",
          }}
        >
          <colgroup>
            <col style={{ width: "170px" }} />
            <col />
          </colgroup>
          <tbody>
            <tr>
              <th style={{ textAlign: "left" }}>
                <label>프로젝트명</label>
              </th>
              <td>
                <Input
                  placeholder="프로젝트명을 입력해주세요."
                  // maxLength={45}
                  style={{ width: "50%", marginRight: "10px" }}
                />

                <Button type="primary">중복확인</Button>
              </td>
            </tr>
            <tr>
              <th style={{ textAlign: "left" }}>
                <label>프로젝트 개요</label>
              </th>
              <td>
                <Input.TextArea style={{ width: "50%" }} />
              </td>
            </tr>
            <tr>
              <th style={{ textAlign: "left" }}>
                <label>PM</label>
              </th>
              <td>
                <Input readOnly style={{ width: "40%", marginRight: "10px" }} />

                <Button type="primary">검색</Button>
              </td>
            </tr>
            <tr>
              <th style={{ textAlign: "left" }}>
                <label>수행조직</label>
              </th>
              <td>
                <Input readOnly style={{ width: "40%", marginRight: "10px" }} />

                <Button type="primary">검색</Button>
              </td>
            </tr>
            <tr>
              <th style={{ textAlign: "left" }}>
                <label>프로젝트 기간</label>
              </th>
              <td>
                <RangePicker />
              </td>
            </tr>
            <tr>
              <th style={{ textAlign: "left" }}>
                <label>사용 Tool 선택</label>
              </th>
              <td>
                <Checkbox.Group>
                  <Checkbox
                    value="Gitlab"
                    style={{ marginRight: "30px", fontSize: "15px" }}
                  >
                    Gitlab
                  </Checkbox>
                  <Checkbox value="Atlassian" style={{ fontSize: "15px" }}>
                    Atlassian (Jira, Confluence)
                  </Checkbox>
                </Checkbox.Group>
              </td>
            </tr>
          </tbody>
        </table>
      </form>
    </>
  );
};

export default StepOne;
